<template>
    <v-container>
        <v-layout row wrap>
            <v-flex xs10 sm7 md7>
                <v-text-field label="作业标题" prepend-icon="event" v-model="homework.title"></v-text-field>
            </v-flex>
            <v-flex xs10 sm7 md7>
                <v-menu
                    ref="menu"
                    :close-on-content-click="false"
                    v-model="menu"
                    :nudge-right="40"
                    :return-value.sync="homework.pubTime"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    min-width="290px"
                >
                    <v-text-field
                    slot="activator"
                    v-model="homework.pubTime"
                    label="截至日期"
                    prepend-icon="event"
                    readonly
                    ></v-text-field>
                    <v-date-picker v-model="homework.pubTime" no-title scrollable>
                    <v-spacer></v-spacer>
                    <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
                    <v-btn flat color="primary" @click="$refs.menu.save(homework.pubTime)">OK</v-btn>
                    </v-date-picker>
                </v-menu>
            </v-flex>
            <v-flex xs10 sm7 md7>
                <v-textarea label="题目简述" prepend-icon="event" v-model="homework.desc"></v-textarea>
            </v-flex>
            <v-flex xs10 sm7 md7>
                <v-textarea label="题目要求" prepend-icon="event" v-model="homework.repu"></v-textarea>
            </v-flex>
            <v-flex xs10 sm7 md7>
                <v-textarea label="注意点" prepend-icon="event" v-model="homework.notice"></v-textarea>
            </v-flex>
        </v-layout>
        <v-flex xs10 sm7 md7>
            <v-btn
              :loading="loading"
              :disabled="loading"
              color="blue-grey"
              class="white--text"
              @click="publish"
            >
            发布
          </v-btn>
        </v-flex>
        <v-snackbar
            v-model="pubBar"
            :timeout="3000"
            top
            >
            发布作业{{homework.title}}成功
            <v-btn
                color="pink"
                flat
                @click="pubBar = false"
            >
                关闭
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
export default {
  data: () => ({
    menu: false,
    loading: false,
    pubBar: false,
    imgs: [
      "https://cdn.vuetifyjs.com/images/cards/desert.jpg",
      "https://cdn.vuetifyjs.com/images/cards/docks.jpg",
      "https://cdn.vuetifyjs.com/images/cards/house.jpg",
      "https://cdn.vuetifyjs.com/images/cards/plane.jpg",
      "https://cdn.vuetifyjs.com/images/cards/sunshine.jpg",
      "https://cdn.vuetifyjs.com/images/cards/road.jpg"
    ],
    homework: {
      title: "",
      pubTime: null,
      desc: "",
      repu: "",
      notice: ""
    }
  }),
  methods: {
    publish() {
      this.loading = true;
      let _this = this;
      let index = parseInt(Math.random() * 6);
      let selectedHomework = {
        title: _this.homework.title,
        desc: _this.homework.desc,
        pubTime: new Date(_this.homework.pubTime).valueOf(),
        repu: _this.homework.repu,
        notice: _this.homework.notice,
        imgUrl: _this.imgs[index]
      };
      this.$postRequest("/apis/teacher/homeworks", selectedHomework)
        .then(res => {
          if (res.data) {
            _this.loading = false;
            _this.pubBar = true;
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
